import { Button, Modal } from 'react-bootstrap';
import React from 'react';
// import { useState } from 'react';
import currentAgeCalChinese from '../scripts/currentAgeCal';
import { cnTrans } from '../scripts/cnTrans';
import { dateStringToDDMMYY } from '../scripts/dateStringToDDMMYY';
function AnimalDetailsModal(props) {
	const { animal } = props;
	if (props.cardsModelState[props.index] === true) {
		return (
			<Modal
				onHide={props.handleClose}
				backdrop="static"
				keyboard={false}
				show={true}

			>
				<Modal.Header closeButton>
					<Modal.Title></Modal.Title>
				</Modal.Header>
				<Modal.Body>
					:{' '}
					{`${animal.animalSpeciesName}<${
						animal.animalSpecies ? `${cnTrans(animal.animalSpecies)}<` : ''
					}${animal.animalGenera ? `${cnTrans(animal.animalGenera)}<` : ''}${
						animal.animalClasses ? `${cnTrans(animal.animalClasses)}` : ''
					}`}
					<br />
					:
					{animal.animalName}
					<br />
					: {animal.animalSpeciesName}
					<br />
					: {cnTrans(animal.animalSex)}
					<br />
					: {currentAgeCalChinese(animal.postDate, animal.animalAge)}

					<br />
					{((animal.urgencyOptions === "urgent") || (animal.urgencyOptions === "mostUrgent")) ?
						<div >: <span className='fw-bold' style={{ color: '#FF0000' }}>{cnTrans(animal.urgencyOptions)}</span></div> : <div>:
							<span> {cnTrans(animal.urgencyOptions)}</span></div>}
						{animal.animalPrice?<div>: <span className='fw-bold'>${animal.animalPrice} HKD</span></div>:<div>: <span className='fw-bold'></span></div>}
					: {animal.healthCondition}
					<br />
					:: {animal.description}
					<br />
					: {animal.requirementToAdopter}
					<br />
					: {animal.creator ? animal.creator.nickname : null}
					<br />
					: {animal.contactInfo}
					<br />
					: {animal.deliveryInfo}
					<br />
					: {dateStringToDDMMYY(animal.postDate)}
				</Modal.Body>
				<Modal.Footer>
					<Button variant="secondary" onClick={props.handleClose}>
						Close
					</Button>
				</Modal.Footer>
			</Modal>
		);
	}
}
export default AnimalDetailsModal;
